const site = {
  name: process.env.VUE_APP_SITE || 'default',
  root: process.env.NODE_ENV === 'production' ? '.' : '',
  install(app) {
    app.config.globalProperties.$site = site
  }
}

console.log(site.name, process.env)

if (site.name === 'varia') {
  site.root = '/solver/static/client'
}

console.log(site)

export default site